#name {
    font-family: 'Patrick Hand', cursive;
    font-size: 12vh;
    font-weight: 700;
    position: absolute;
    width: auto;
    display: inline-block;
    color: black;
    top: 45vh;
    letter-spacing: 0.6vh;
    overflow: hidden;
    border-right: .15em solid black;
    white-space: nowrap;
    margin: 0 auto;
    display: inline-block;
    /* letter-spacing: .15em; */
    -webkit-animation:
            typing 2.5s steps(25, end),
            blink-caret .5s step-end 2, 
            textgrowth 1.5s 1.5s 1 alternate,
            slide-up 1.5s 1.5s 1 alternate;
            animation:
            typing 2.5s steps(25, end),
            blink-caret .5s step-end 2, 
            textgrowth 1.5s 1.5s 1 alternate,
            slide-up 1.5s 1.5s 1 alternate;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards; 
}

@-webkit-keyframes textgrowth {
    0% {
    font-size: 12vh;
    }
    100% {
    font-size: 5vh;
    }
}

@keyframes textgrowth {
    0% {
    font-size: 12vh;
    }
    100% {
    font-size: 5vh;
    }
}

@-webkit-keyframes slide-up {
    from {
        /* opacity: .25; */
        top: 45vh;
    }
    to {

        /* opacity: 1; */
        top: 25vh;
    }
}

@keyframes slide-up {
    from {
        /* opacity: .25; */
        top: 45vh;
    }
    to {

        /* opacity: 1; */
        top: 25vh;
    }
}
/* The typing effect */
@-webkit-keyframes typing {
    from { max-width: 0 }
    to { max-width: 100% }
}
@keyframes typing {
    from { max-width: 0 }
    to { max-width: 100% }
}

/* The typewriter cursor effect */
@-webkit-keyframes blink-caret {
    from, to { border-color: transparent }
    50% { border-color: #DF7570}
}
@keyframes blink-caret {
    from, to { border-color: transparent }
    50% { border-color: #DF7570}
}

@-webkit-keyframes bunny-show {
    from {
        opacity: 0;
    }
    to {
        opacity: 0;
    }
}

@keyframes bunny-show {
    from {
        opacity: 0;
    }
    to {
        opacity: 0;
    }
}

@-webkit-keyframes fadeInUp {
    0% {
        opacity: 0;
        -webkit-transform: translate3d(0,75%,0);
                transform: translate3d(0,75%,0);
    }
    100% {
        opacity: 1;
        -webkit-transform: none;
                transform: none;
    }
}

@keyframes fadeInUp {
    0% {
        opacity: 0;
        -webkit-transform: translate3d(0,75%,0);
                transform: translate3d(0,75%,0);
    }
    100% {
        opacity: 1;
        -webkit-transform: none;
                transform: none;
    }
}
