@-webkit-keyframes infinite-spinning {
    0% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    25% {
      -webkit-transform: rotate(5deg);
              transform: rotate(5deg);
    }
    /* 50% {
        transform: rotate(0deg);
    } */
    75% {
        -webkit-transform: rotate(-5deg);
                transform: rotate(-5deg);
    }
    100% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
  }


@keyframes infinite-spinning {
    0% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    25% {
      -webkit-transform: rotate(5deg);
              transform: rotate(5deg);
    }
    /* 50% {
        transform: rotate(0deg);
    } */
    75% {
        -webkit-transform: rotate(-5deg);
                transform: rotate(-5deg);
    }
    100% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
  }

